import React, { useContext, useState } from "react"
import { Link, navigate } from "gatsby"
import MenuContext from "../MenuContext"
import { motion } from "framer-motion"
import { FiChevronDown as Chevron } from "react-icons/fi"
import { HamburgerStyles, LogoStyles, NavModuleStyles, NavTopLevel, SubNavStyles } from "./NavModuleStyles"
import { barOneVariants, barThreeVariants, barTwoVariants, menuList, subMenuNavVariants } from "./NavAnim"
import { currentArtist, menuItems } from "../../constants/globals"
import { Autocomplete, TextField } from "@mui/material"

const NavModule = () => {
    const [isOpen, setNav] = useContext(MenuContext)
    const [subNavIsOpen, setSubNav] = useState(false)

    const toggleNav = () => {
        setNav((isOpen) => !isOpen)
    }

    const toggleSubNav = () => {
        setSubNav((subNavIsOpen) => !subNavIsOpen)
    }

    const title = currentArtist().name
    const searchOptions = currentArtist().artwork.map((artwork) => {
        return { label: artwork.title, id: artwork.slug }
    })

    return (
        <NavModuleStyles>
            <div className="nav">
                <div className="container">
                    <HamburgerStyles
                        initial="closed"
                        animate={isOpen ? "open" : "closed"}
                        onClick={toggleNav}
                        onKeyDown={toggleNav}
                        aria-label={isOpen ? "Close Menu" : "Open Menu"}
                        className={isOpen ? " open" : ""}
                    >
                        <motion.span className="bar" variants={barOneVariants}></motion.span>
                        <motion.span className="bar" variants={barTwoVariants}></motion.span>
                        <motion.span className="bar" variants={barThreeVariants}></motion.span>
                    </HamburgerStyles>

                    {/* TODO maybe only show for me */}
                    <Autocomplete
                        onChange={(event, value) => {
                            // noinspection JSIgnoredPromiseFromCall,JSUnresolvedVariable
                            navigate("/artwork/" + value.id)
                        }}
                        autoSelect={true}
                        id="artwork-search"
                        options={searchOptions}
                        sx={{ width: 300 }}
                        renderInput={(params) => <TextField {...params} className={"xyz"} label="Search" />}
                    />

                    {title && (
                        <LogoStyles>
                            <Link to="/">
                                {title}
                                <span>.</span>
                            </Link>
                        </LogoStyles>
                    )}
                </div>
            </div>
            <motion.div
                initial="closed"
                animate={isOpen ? "open" : "closed"}
                variants={menuList}
                transition={{ type: "ease", stiffness: 50, velocity: 50 }}
                className="menu"
            >
                <NavTopLevel>
                    {menuItems.map((item, index) => (
                        <li key={index}>
                            <Link
                                onClick={toggleNav}
                                onKeyDown={toggleNav}
                                to={item.path}
                                activeClassName="menu__item--active"
                            >
                                {item.text}
                                <span>.</span>
                            </Link>
                        </li>
                    ))}
                    {/* TODO */}
                    {false && ( // TODO was featuredProduct
                        <li className={subNavIsOpen ? "open" : "closed"}>
                            <button type="button" onClick={toggleSubNav} onKeyDown={toggleSubNav}>
                                Products<span>.</span>
                                <Chevron />
                            </button>

                            <SubNavStyles
                                initial="closed"
                                animate={subNavIsOpen ? "open" : "closed"}
                                variants={subMenuNavVariants}
                            >
                                <li>
                                    <Link onClick={toggleNav} onKeyDown={toggleNav} to="/products">
                                        All Products<span>.</span>
                                    </Link>
                                </li>
                                <hr />
                                {[].map((item, index) => {
                                    // TODO was featuredProduct
                                    const { gatsbyPath, title } = item
                                    return (
                                        <li key={index}>
                                            <Link onClick={toggleNav} onKeyDown={toggleNav} to={gatsbyPath}>
                                                {title}
                                                <span>.</span>
                                            </Link>
                                        </li>
                                    )
                                })}
                            </SubNavStyles>
                        </li>
                    )}
                </NavTopLevel>
            </motion.div>
        </NavModuleStyles>
    )
}

export default NavModule
